/* General */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#main {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: auto;
}

.upper {
  max-height: 30vh;
}

#grid {
  margin: auto;
  max-height: 70vh;
}

/* Top controls */

.controls {
  display: flex;
  justify-content: flex-start;
  margin-top: 5px;
  padding-top: 10px;
}

.controls .form-group {
  margin: 0 20px auto 0;
}

.controls button {
  margin-right: 5px;
}

.form-group {
  padding: 5px 0;
}

.form-group>div {
  margin: 2px;
}

input[type="number"] {
  margin-left: 5px;
}

/* Color picker */

.form-color-picker {
  position: relative;
}

.color-picker {
  position: absolute;
  top: 100%;
}